* {
	outline: none !important;
	min-height: 0;
	min-width: 0;
}

html,
body {
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	line-height: 1.65;
	font-weight: normal;
	color: $text-color;
	min-width: 360px;
}

.red {
	color: #E44B4B;
}

h1,
.h1 {
	font-weight: bold;
	font-size: 50px;
	line-height: 100%;
	color: $text-color;
	margin: 0 0 24px 0;
	font-family: 'Roboto Slab';
}

h1:last-child,
.h1:last-child {
	margin-bottom: 0 !important;
}

h2,
.h2 {
	font-family: 'Roboto Slab';
	font-weight: bold;
	font-size: 40px;
	line-height: 110%;
	/* identical to box height, or 44px */


	color: #2D2E39;
	margin: 45px 0 19px 0;
}

h1:first-child,
.h1:first-child,
h2:first-child,
.h2:first-child,
h3:first-child,
.h3:first-child,
h4:first-child,
.h4:first-child {
	margin-top: 0 !important;
}

.over-wrapper a,
.over-wrapper a:focus {
	color: $blue-color;
	text-decoration: none;
	transition: all .15s ease;
}

.over-wrapper a:hover {
	text-decoration: underline;
}

p {
	margin: 0 0 10px 0;
}

p:last-child {
	margin-bottom: 0;
}

strong {
	font-weight: bold !important;
}

.over-wrapper ul {
	margin: 0 0 15px 0;
	padding: 0;
	list-style: none;
}

.over-wrapper ul:last-child {
	margin-bottom: 0;
}

.over-wrapper ul > li {
	padding: 0 0 0 32px;
	margin: 0 0 10px 0;
	position: relative;
}

.over-wrapper ul > li:before {
	content: "";
	width: 16px;
	height: 15px;
	background: url(../images/li-bullet.svg) 0 0 no-repeat;
	position: absolute;
	left: 0;
	top: 5px;
}

.over-wrapper ol,
.modal ol {
	padding: 0;
	margin: 0 0 15px 0;
	counter-reset: item;
	list-style: none;
}

.over-wrapper ol > li,
.modal ol > li {
	position: relative;
	padding: 0 0 0 45px;
	margin: 0 0 15px 0;
}

.over-wrapper ol > li:before,
.modal ol > li:before {
	color: #FDB930;
	font-family: 'Roboto';
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
	/* identical to box height */
	position: absolute;
	left: 0;
	top: -1px;
	content: "0" counter(item) " ";
	counter-increment: item;
	letter-spacing: .1em;
	color: transparent;
	-webkit-text-fill-color: transparent;
	/* Will override color (regardless of order) */
	-webkit-text-stroke-width: 1px;
	-webkit-text-stroke-color: #6E9DB4;
}

.over-wrapper ol > li:nth-child(n+10):before {
	content: counter(item) " ";
}

.over-wrapper ol > li:after {
	display: block;
	content: "";
	width: 30px;
	height: 4px;
	background: url(../images/ol-deco.svg) 0 0 no-repeat;
	position: absolute;
	left: 34px;
	top: 11px;
}

.over-wrapper ol.ol-alt > li {
	padding-left: 40px;
}

.over-wrapper ol.ol-alt > li:before {
	color: #EC5F65;
}

.over-wrapper ol.ol-alt > li:after {
	display: none;
}

.ol-red {
	font-weight: 600;
	color: #EC5F65;
}

.over-wrapper table {
	width: 100%;
	font-family: 'Roboto';
	font-size: 16px;
	line-height: 26px;
	color: #272E33;
}

.over-wrapper table th {
	border-top: 2px solid #FDB930;
	border-bottom: 2px solid #FDB930;
	font-family: 'Roboto';
	font-weight: 500;
	font-size: 18px;
	line-height: 26px;
	color: #000000;
	vertical-align: top;
	padding: 20px 15px 20px 15px;
}

.over-wrapper table td {
	border-bottom: 1px solid #C4C4C4;
	vertical-align: top;
	padding: 22px 15px 22px 15px;
}

.over-wrapper table th:first-child,
.over-wrapper table td:first-child {
	padding-left: 0;
}

.over-wrapper table th:last-child,
.over-wrapper table td:last-child {
	padding-right: 0;
}

table.tech-table {
	border-top: 1px solid #EDEFF7;
	margin: 30px 0 0 0;
}

table.tech-table td {
	border-bottom: 1px solid #EDEFF7;
	padding: 12px 15px 11px 15px;
}

table.tech-table th:first-child,
table.tech-table td:first-child {
	padding-left: 0;
}

table.tech-table th:last-child,
table.tech-table td:last-child {
	padding-right: 0;
}

table.tech-table td.name {
	color: #9EAAC0;
}

table.tech-table td.val {
	font-weight: bold;
	color: $text-color;
	text-align: right;
}

/* Buttons */
.btn,
.btn:focus {
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	border: none;
	-webkit-appearance: none;
	white-space: normal;
}

.btn:active {
	box-shadow: none;
}

.btn-fwd,
.btn-fwd:focus {
	font-size: 16px;
	line-height: 24px;
	font-weight: bold;
	text-transform: uppercase;
	padding: 0 0 0 45px;
	position: relative;
	color: #6E9DB4;
}

.btn-fwd:hover {
	color: #FF6F53;
}

.btn-fwd:before {
	content: "";
	width: 28px;
	height: 51px;
	background: url(../images/ico-btn-fwd.png) 0 0 no-repeat;
	background-size: 28px auto;
	position: absolute;
	left: 0;
	top: -11px;
}

.btn-fwd:hover:before {
	background-position: 0 -51px;
}

.btn-1,
.btn-1:focus {
	border: 1px solid #fff;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 16px;
	color: #fff !important;
	text-transform: uppercase;
	transition: all .15s ease;
	padding: 19px 50px 17px 50px;
}

.btn-cont {
	display: flex;
	justify-content: center;
}

.btn-dl,
.btn-dl:focus {
	border: 1px solid #fff;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 16px;
	color: #fff !important;
	text-transform: uppercase;
	transition: all .15s ease;
	padding: 19px 30px 17px 30px;
}

.btn-dl-arrow {
	width: 17px;
	height: 22px;
	margin: 0 10px 0 0;
	position: relative;
}

.btn-dl-arrow:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/btn-dl-ico.svg) 0 0 no-repeat;
	transition: all .15s ease;
}

.btn-load,
.btn-load:focus {
	border: 1px solid #003594;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 16px;
	color: #003594 !important;
	text-transform: uppercase;
	transition: all .15s ease;
	padding: 19px 30px 17px 30px;
}

.btn-load-arrow {
	width: 17px;
	height: 22px;
	margin: -2px 10px 0 0;
	position: relative;
}

.btn-load-arrow:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/btn-load-ico.svg) 0 0 no-repeat;
	transition: all .15s ease;
}

.btn-all,
.btn-all:focus {
	border: 1px solid #fff;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 14px;
	color: #fff !important;
	text-transform: uppercase;
	transition: all .15s ease;
	padding: 19px 30px 18px 30px;
}

.btn-all-arrow {
	width: 9px;
	height: 12px;
	margin: 4px 0 0 13px;
	position: relative;
}

.btn-all-arrow:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/btn-all-arrow.svg) 0 0 no-repeat;
	transition: all .15s ease;
}

.btn-submit,
.btn-submit:focus {
	background: #003594;
	color: #fff !important;
	font-size: 16px;
	font-weight: 500;
	text-transform: uppercase;
	padding: 19px 30px 17px 30px;
}

.btn-submit-arrow {
	background: url(../images/btn-submit-arrow.svg) right 0 no-repeat;
	width: 60px;
	height: 20px;
	margin: 1px 0 0 10px;
	flex-shrink: 0;
}

.btn-submit-alt,
.btn-submit-alt:focus {
	padding-left: 50px;
	padding-right: 50px;
}

.btn-submit-alt .btn-submit-arrow {
	background: url(../images/btn-submit-alt-arrow.svg) right 0 no-repeat;
	width: 47px;
	height: 16px;
	margin: 3px 0 0 10px;
	flex-shrink: 0;
}

.expandable-trigger-wrapper {
	margin: 48px 0 0 0;
}

.expandable-trigger,
.expandable-trigger:focus {
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	text-transform: uppercase;
	padding: 0 66px 0 0;
	position: relative;
	color: #6E9DB4;
	text-decoration: none !important;
	transition: all .15s ease;
	display: inline-block;
}

.expandable-trigger:hover {
	color: #FF6F53;
}

.expandable-trigger-arrow {
	position: absolute;
	right: 0;
	top: -11px;
	width: 45px;
	height: 45px;
}

.expandable-trigger-arrow:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/expandable-trigger-ico.svg) 0 0 no-repeat;
	transition: all .15s ease;
}

.expandable-trigger-arrow:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/expandable-trigger-ico-hover.svg) 0 0 no-repeat;
	transition: all .15s ease;
	opacity: 0;
}

.expandable-trigger:hover .expandable-trigger-arrow:before {
	opacity: 0;
}

.expandable-trigger:hover .expandable-trigger-arrow:after {
	opacity: 1;
}

.expandable-trigger.active .expandable-trigger-arrow:before,
.expandable-trigger.active .expandable-trigger-arrow:after {
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.expandable-trigger-text {
	display: inline-block;
}

.btn-back,
.btn-back:focus {
	width: 60px;
	height: 60px;
	position: relative;
	border: 1px solid #FF6F53;
	border-radius: 50%;
	-ms-flex-positive: 0;
	flex-grow: 0;
	-ms-flex-negative: 0;
	flex-shrink: 0;
}

.btn-back:after {
	content: "";
	width: 8px;
	height: 14px;
	background: url(../images/ico-btn-back.svg) 0 0 no-repeat;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	transform: translateX(-50%) translateY(-50%);
}

.btn-order {
	background: #FF6F53;
	border-radius: 0px 0px 90px 0px;
	width: 100%;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	letter-spacing: -0.03em;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 27px 15px 49px 33px;
	white-space: normal;
	text-align: left;
	display: block;
	position: relative;
}

.btn-order:after {
	content: "";
	position: absolute;
	width: 128px;
	height: 43px;
	background: url(../images/btn-order-arrow.svg) 0 0 no-repeat;
	position: absolute;
	left: 33px;
	bottom: 6px;
}

.btn-order:hover {
	background: #EA5134;
}

/* Buttons END */

.slider-wrapper {
	margin: 0 -15px;
	position: relative;
}

.slick-slide {
	padding: 0 15px;
}



.slick-arrow {
	border: none;
	display: block;
	-webkit-appearance: none;
	background: #F9FAFE;
	width: 65px;
	height: 65px;
	position: absolute;
	top: 50%;
	margin-top: -33px;
	cursor: pointer;
	font-size: 0;
	transition: all .3s ease;
	padding: 0;
	z-index: 200;
}

.slick-arrow.slick-disabled {
	opacity: 0.3 !important;
	cursor: default;
}

.slick-arrow:before {
	content: "";
	position: absolute;
	width: 13px;
	height: 22px;
	top: 50%;
	margin-top: -11px;
	background-repeat: no-repeat;
	transition: all .2s ease;
	z-index: 10;
	background-position: center center;
}

.slick-prev:before {
	left: 23px;
	background-image: url(../images/product-pic-slider-prev.svg);
}

.slick-next:before {
	right: 23px;
	background-image: url(../images/product-pic-slider-next.svg);
}

.slick-prev {
	left: -0px;
}

.slick-next {
	right: -0px;
}

.slider-count {
	font-size: 20px;
	line-height: 30px;
	font-weight: 300;
	position: absolute;
	left: 114px;
	bottom: -65px;
}

.slider-count-cur {
	font-weight: bold;
}

.slider-count-sep {
	padding: 0 6px;
}

/* Modals */

.modal {
	background: rgba(45,46,57,.9);
}

.modal-backdrop {
	background: $text-color;
	background: none;
}

.modal-backdrop.show {
	opacity: .9;
}

.modal-dialog {
	pointer-events: auto;
}

.side-modal {
	padding-right: 0 !important;
}

.side-modal .modal-dialog {
	margin: 0 auto 0 0;
	transform: translate(-100%, 0) !important;
	max-width: 100%;
	min-height: 100%;
}

.side-modal.show .modal-dialog {
	transform: translate(0, 0) !important;
}

.side-modal .modal-content {
	border: none;
	border-radius: 0;
	background: none;
	min-height: 100%;
	padding: 0 90px 0 0;
	flex-grow: 1;
}

.side-modal .close {
	position: fixed;
	right: 45px;
	top: 35px;
	cursor: pointer;
	z-index: 100;
	opacity: 1 !important;
}

.modal .close svg * {
	transition: all .15s ease;
}

.modal .close:hover svg *[stroke] {
	stroke: #003594;
}

.modal .close:hover svg *[fill] {
	fill: #003594;
}

.side-modal .modal-content .row {
	-ms-flex-wrap: nowrap;
	flex-wrap: nowrap;
	min-height: 100%;
}

.side-modal-r {
	padding: 0 15px;
	flex: 0 0 280px;
	max-width: 280px;
	overflow: hidden;
}

.side-modal-l {
	padding: 0 15px;
	min-height: 100%;
	//max-width: 1020px;
	flex-grow: 1;
}

.serv-modal-col {
	max-width: 990px;
	margin-left: auto;
	margin-right: auto;
}

.side-modal-l-inner {
	background: #fff;
	padding: 0;
	min-height: 100%;
}

.form-modal {
	padding: 30px;
}

.form-modal-header {
	margin: 0 0 30px 0;
}

.form-modal-header h2,
.form-modal-header .h2 {
	font-family: 'Roboto Slab';
	font-size: 40px;
	line-height: 1.1;
	margin: 0;
	font-weight: bold;
	color: $text-color;
}

.form-modal .modal-body {
	padding: 0;
}

.form-modal .modal-dialog {
	max-width: unset;
	height: auto;
	min-height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-ms-flex-pack: center;
	justify-content: center;
	margin: 0 auto;
}

.form-modal .modal-header h2,
.form-modal .modal-header .h2 {
	font-weight: bold;
	font-size: 45px;
	line-height: 55px;
	letter-spacing: -0.03em;
	text-transform: uppercase;
